"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { ScrollArea } from "@/components/ui/scroll-area"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

// Mock data for demonstration
const MOCK_CONVERSATIONS = [
  {
    id: "1",
    customer: {
      id: "C1",
      name: "John Doe",
      orderId: "ORD001",
    },
    lastMessage: "When will my suit be ready for fitting?",
    lastMessageTime: "10:30 AM",
    unread: true,
    status: "active",
  },
  {
    id: "2",
    customer: {
      id: "C2",
      name: "Jane Smith",
      orderId: "ORD002",
    },
    lastMessage: "Thanks for the update!",
    lastMessageTime: "Yesterday",
    unread: false,
    status: "active",
  },
  {
    id: "3",
    customer: {
      id: "C3",
      name: "Mike Johnson",
      orderId: "ORD003",
    },
    lastMessage: "I need to change my measurement appointment.",
    lastMessageTime: "2 days ago",
    unread: true,
    status: "pending",
  },
]

const MOCK_MESSAGES: Record<string, Message[]> = {
  "1": [
    {
      id: "m1",
      sender: "customer",
      message: "Hi, I wanted to ask about my suit order #ORD001",
      timestamp: "10:00 AM",
    },
    {
      id: "m2",
      sender: "admin",
      message: "Hello! Yes, I can help you with that. What would you like to know?",
      timestamp: "10:15 AM",
    },
    {
      id: "m3",
      sender: "customer",
      message: "When will my suit be ready for fitting?",
      timestamp: "10:30 AM",
    },
  ],
}

interface Message {
  id: string
  sender: "customer" | "admin"
  message: string
  timestamp: string
}

function ChatMessage({ message }: { message: Message }) {
  const isAdmin = message.sender === "admin"

  return (
    <div className={`flex ${isAdmin ? "justify-end" : "justify-start"} mb-4`}>
      <div
        className={`max-w-[80%] ${
          isAdmin
            ? "bg-black text-white"
            : "bg-gray-100 text-black"
        } rounded-lg px-4 py-2`}
      >
        <div className="flex items-center gap-2 mb-1">
          <span className="font-medium text-sm">
            {isAdmin ? "You" : "Customer"}
          </span>
          <span className="text-xs opacity-70">{message.timestamp}</span>
        </div>
        <p className="text-sm">{message.message}</p>
      </div>
    </div>
  )
}

export default function AdminChat() {
  const [conversations, setConversations] = useState(MOCK_CONVERSATIONS)
  const [selectedConversation, setSelectedConversation] = useState<string | null>(null)
  const [newMessage, setNewMessage] = useState("")
  const [filter, setFilter] = useState<"all" | "active" | "pending">("all")

  const filteredConversations = conversations.filter(conv => 
    filter === "all" ? true : conv.status === filter
  )

  const currentMessages = selectedConversation 
    ? MOCK_MESSAGES[selectedConversation as keyof typeof MOCK_MESSAGES] || []
    : []

  const currentConversation = conversations.find(c => c.id === selectedConversation)

  const handleSendMessage = (e: React.FormEvent) => {
    e.preventDefault()
    if (!newMessage.trim() || !selectedConversation) return

    const message: Message = {
      id: Date.now().toString(),
      sender: "admin",
      message: newMessage.trim(),
      timestamp: new Date().toLocaleTimeString([], {
        hour: "2-digit",
        minute: "2-digit",
      }),
    }

    // In production, implement API call to send message
    console.log("Sending message:", message)
    setNewMessage("")
  }

  return (
    <div className="h-[calc(100vh-8rem)]">
      <div className="grid grid-cols-12 h-full gap-6">
        {/* Conversations List */}
        <div className="col-span-4 bg-white rounded-lg border">
          <div className="p-4 border-b">
            <h2 className="font-semibold mb-4">Conversations</h2>
            <Select
              value={filter}
              onValueChange={(value: "all" | "active" | "pending") => setFilter(value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Filter conversations" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Conversations</SelectItem>
                <SelectItem value="active">Active</SelectItem>
                <SelectItem value="pending">Pending</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <ScrollArea className="h-[calc(100%-5rem)]">
            <div className="p-2">
              {filteredConversations.map((conv) => (
                <div
                  key={conv.id}
                  className={`p-3 rounded-lg cursor-pointer transition-colors ${
                    selectedConversation === conv.id
                      ? "bg-gray-100"
                      : "hover:bg-gray-50"
                  }`}
                  onClick={() => setSelectedConversation(conv.id)}
                >
                  <div className="flex justify-between items-start mb-1">
                    <h3 className="font-medium">{conv.customer.name}</h3>
                    <span className="text-xs text-gray-500">
                      {conv.lastMessageTime}
                    </span>
                  </div>
                  <p className="text-sm text-gray-600 mb-1">
                    Order: {conv.customer.orderId}
                  </p>
                  <p className="text-sm text-gray-500 truncate">
                    {conv.lastMessage}
                  </p>
                  {conv.unread && (
                    <div className="mt-2">
                      <span className="px-2 py-1 text-xs bg-black text-white rounded-full">
                        New
                      </span>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </ScrollArea>
        </div>

        {/* Chat Area */}
        <div className="col-span-8 bg-white rounded-lg border">
          {selectedConversation ? (
            <div className="h-full flex flex-col">
              {/* Chat Header */}
              <div className="p-4 border-b">
                <div className="flex justify-between items-center">
                  <div>
                    <h2 className="font-semibold">
                      {currentConversation?.customer.name}
                    </h2>
                    <p className="text-sm text-gray-500">
                      Order ID: {currentConversation?.customer.orderId}
                    </p>
                  </div>
                  <Button
                    variant="outline"
                    onClick={() => {
                      // In production, implement order details view
                      alert("View order details - Coming soon!")
                    }}
                  >
                    View Order
                  </Button>
                </div>
              </div>

              {/* Messages Area */}
              <ScrollArea className="flex-1 p-4">
                <div className="space-y-4">
                  {currentMessages.map((message) => (
                    <ChatMessage key={message.id} message={message} />
                  ))}
                </div>
              </ScrollArea>

              {/* Message Input */}
              <form
                onSubmit={handleSendMessage}
                className="p-4 border-t flex gap-2"
              >
                <Input
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder="Type your message..."
                  className="flex-1"
                />
                <Button
                  type="submit"
                  className="bg-black text-white hover:bg-gray-900"
                  disabled={!newMessage.trim()}
                >
                  Send
                </Button>
              </form>
            </div>
          ) : (
            <div className="h-full flex items-center justify-center text-gray-500">
              Select a conversation to start chatting
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
